.Board {
    height: 100%;
    display: flex;
	flex-direction: row;
	padding-bottom: 1vh;
	padding-top: 1vh;	
	margin: 0;
	/* flex-wrap: wrap; */
  /* justify-content: space-around; */
	/* align-content: space-around; */
    /* overflow-x: auto; */
}
 
.Stage {
    background: #e2e5ed;
    min-width: 20vw;
    height: fit-content;
    margin: 10px;
    /* margin-right: 0; */
    border-radius: 2px;
    border: 1px solid rgba(0, 0, 0, 0.12);
}

.Stage-Title {
    cursor: pointer;
    padding: 10px;
    overflow-wrap: break-word;
    font-weight: bold;
    text-align: left;
    font-size: 14px;
}
.Task {
    position: relative;
    cursor: pointer;
    background: white;
    margin: 5px;
    padding: 10px;
    border-radius: 2px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    box-shadow: 0 1px 0 rgba(9, 45, 66, 0.25);
    font-size: 15px;
    overflow-wrap: break-word;
    min-height: 18px;
}

.Task:hover {
    background: #f5f6f7;
}

.Toggle-Add-Task {
    cursor: pointer;
    padding: 10px;
    color: #6b808c;
    border-radius: 0 0 2px 2px;
    display: flex;
    align-items: center;
}
  
.Toggle-Add-Task:hover {
    background-color: rgba(9, 45, 66, 0.13);
    color: #17394d;
    text-decoration: underline;
}

.Toggle-Add-Task ion-icon {
    margin-right: 2px;
}

.Edit-Task {
    min-height: 50px;
}

.Task-Card .Task:hover {
    background: white;
}
  
  .Edit-Task-Textarea {
    border: none;
    resize: none;
    outline: none;
    width: 100%;
    font-size: 15px;
  }

  .Edit-Buttons {
    display: flex;
    width: 100%;
    justify-content: space-around;
  }
  
  .Edit-Button {
    cursor: pointer;
    box-shadow: 0 1px 0 0 #3f6f21;
    width: fit-content;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 5px 5px;
    border-radius: 2px;
    color: white;
    font-weight: 700;
    outline: none;
    font-size: 13px;
  }
  
  .Edit-Button:hover {
    opacity: 0.7;
  }
  
  